header {
  height: 100vh;
  padding-top: 7rem;
  /* overflow: hidden; */
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
  transition: var(--transition);
}

.header__container-bottom {
  text-align: center;
  position: relative;
}

/* CTA */

.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/*  Socials */

.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  left: 70%;
  transform: translateX(-30%);
}

/* .header__socials {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}  */

/* ME */
.me img {
  border-radius: 2rem;
  max-width: 100%;
  height: auto;
}

.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 30rem;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 1rem;
  border-radius: 2rem 2rem 0 0;
  overflow: hidden;
  padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/* scroll */

.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

@media screen and (max-width: 800px) {
  header {
    height: 100vh;
  }

  .header__socials {
    display: inline-flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
    padding-top: 2rem;
    margin-top: 2rem;
    left: 50%;
    transform: translateY(-50%);
    font-size: 1.2rem;
  }
}
